import Accordion from "@components/common/Accordion";
import { ROUTES } from "@constants/routes";
import { useLocation } from "react-router-dom";
import styled from "styled-components";
import { Status, Wrapper } from "@googlemaps/react-wrapper";
import { useEffect, useRef, useState } from "react";
import Survey from "./Survey";

const render = (status: Status) => {
  switch (status) {
    case Status.LOADING:
      return <>로딩중...</>;
    case Status.FAILURE:
      return <>에러 발생</>;
    case Status.SUCCESS:
      return <GoogleMap />;
  }
};

const GoogleMap = () => {
  const mapRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    if (mapRef.current) {
      const map = new google.maps.Map(mapRef.current, {
        center: { lat: 35.1796, lng: 129.0756 }, // Busan's coordinates
        zoom: 12,
      });

      // Example of adding a marker to the map
      new google.maps.Marker({
        position: { lat: 35.1796, lng: 129.0756 },
        map: map,
        title: "Busan",
      });
    }
  }, []);

  return <MapContainer ref={mapRef} />;
};

export const Map = () => {
  const location = useLocation();
  const GOOGLE_MAPS_API_KEY = "" + process.env.REACT_APP_GOOGLE_MAPS_API_KEY;

  return (
    <PageWrapper>
      <ContentWrapper>
        {location.pathname === ROUTES.STAY && (
          <Accordion
            title={"AI 기반 주거지 추천 서비스"}
            subtitle={"당신이 찾는 조건을 선택하세요."}
          >
            <Survey />
          </Accordion>
        )}
        {location.pathname === ROUTES.TEAVEL && (
          <Accordion title={"관광"} subtitle={"관광 데이터"}>
            이건 관광
          </Accordion>
        )}
        {location.pathname === ROUTES.AROUND && (
          <Accordion title={"내 주변"} subtitle={"내 주변 데이터"}>
            이건 내주변
          </Accordion>
        )}
        <MapWrapper>
          <Wrapper
            apiKey={GOOGLE_MAPS_API_KEY} // 여기에 발급받은 API 키를 넣으세요
            render={render}
          />
        </MapWrapper>
      </ContentWrapper>
    </PageWrapper>
  );
};

export default Map;

const PageWrapper = styled.div`
  display: flex;
  height: 100vh;
  width: 100vw;
`;

const ContentWrapper = styled.div`
  display: flex;
  overflow: hidden;
`;

const MapWrapper = styled.div`
  width: 100vw;
`;

const MapContainer = styled.div`
  width: 100%;
  height: 100%;
`;
